<template>
  <v-container>
    <v-card class="mt-5">
      <v-card-title>
        <h3 class="mx-auto text-center">
          Meeting Requests
          <v-btn
            text
            fab
            target="_blank"
            href="https://docs.google.com/spreadsheets/d/1B0NY5O5LS_Nst4SpHfktMKVsdqozaWzpC9GWaSSE-m0/edit?usp=sharing"
            ><v-icon color="green" large>mdi-google-spreadsheet</v-icon></v-btn
          >
        </h3>
        <v-spacer />
        <v-btn class="primary" tile large @click="mrq = true"
          >Schedule a Meeting</v-btn
        >
      </v-card-title>
      <v-card-text>
        <v-simple-table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Reason</th>
              <th>Reply</th>
              <th>Date & Time</th>
              <th>Status</th>
              <th>Amount</th>
              <th>Duration</th>
              <th>Link</th>
              <th>Edit</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(m, i) in meetings" :key="i">
              <td>{{ m.name }}</td>
              <td>{{ m.reason }}</td>
              <td>{{ m.reply }}</td>
              <td>
                {{
                  m.datetime
                    ? new Date(m.datetime).toLocaleString()
                    : "Not Given"
                }}
              </td>
              <td>{{ m.status ? "Responded" : "Pending" }}</td>
              <td>{{ m.amount }} ৳</td>
              <td>{{ m.duration }} Minutes</td>
              <td>
                <v-btn
                  v-if="m.link"
                  :href="m.link"
                  target="_blank"
                  fab
                  small
                  dark
                >
                  <v-icon>mdi-video-plus</v-icon>
                </v-btn>
              </td>
              <td>
                <v-btn @click="openForEdit(m)" fab small dark>
                  <v-icon>mdi-circle-edit-outline</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-card-text>
    </v-card>
    <v-dialog v-model="mrq" max-width="600">
      <v-card>
        <v-card-title>
          <h2 class="mx-auto text-center">Schedule a Meeting</h2>
        </v-card-title>
        <v-card-text>
          <v-form ref="form" lazy-validation v-model="valid">
            <v-text-field
              type="datetime-local"
              label="Meeting Start Time"
              v-model="meeting.datetime"
              outlined
              dense
            ></v-text-field>
            <v-text-field
              type="number"
              label="Meeting Gap Duration(minutes)"
              v-model="meeting.gap"
              outlined
              dense
            ></v-text-field>
            <v-text-field
              v-model="meeting.link"
              outlined
              dense
              label="Meeting/Recorded Link"
            >
            </v-text-field>
            <v-textarea v-model="meeting.reason" outlined label="Reason" />
          </v-form>
          <div>
            <v-spacer></v-spacer>

            <v-text-field
              v-model="search"
              label="Search Student"
              append-icon="mdi-magnify"
            />
          </div>
          <v-data-table
            v-model="selected"
            :headers="headers"
            :items="students"
            item-key="roll"
            show-select
            :search="search"
            class="elevation-1"
          >
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="mx-auto primary"
            large
            tile
            :loading="loading"
            @click="meetingRequest"
            >Submit</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="edititem" max-width="600">
      <v-card>
        <v-card-title
          ><h2 class="mx-auto text-center">
            Update Meeting Status
          </h2></v-card-title
        >
        <v-card-text>
          <v-text-field
            type="datetime-local"
            v-model="currentMeeting.datetime"
            label="Meeting time"
          />
          <v-text-field
            v-model="currentMeeting.link"
            label="Meeting/Recorded Link"
          ></v-text-field>
          <v-textarea
            v-model="currentMeeting.reply"
            outlined
            label="Reply"
          ></v-textarea>
          <v-checkbox v-model="currentMeeting.status" label="Approved?" />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" large tile @click="edititem = false"
            >Close</v-btn
          >
          <v-btn
            class="success"
            @click="updateMeeting"
            :loading="loading"
            large
            tile
            >Update</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      currentMeeting: {},
      selected: [],
      search: "",
      edititem: false,
      students: [],
      singleSelect: false,
      meeting: {},
      valid: true,
      loading: false,
      meetings: [],
      mrq: false,
      headers: [{ text: "Name", value: "name" }],
    };
  },
  methods: {
    meetingRequest() {
      let { datetime, link, gap } = this.meeting;
      let candidates = this.selected.map((c) => {
        let cd = {
          roll: c.roll,
          datetime: new Date(datetime),
        };
        datetime = new Date(datetime).getTime() + gap * 60000;
        return cd;
      });

      fetch(
        "https://script.google.com/macros/s/AKfycbxCVghzqN-sCd9bsd9i8zlyrgmyMdDaNtqcjwygaZnsM-0FQCXtkRdohybS8uThVA_Q/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            type: "all",
            link,
            roll: JSON.stringify(candidates),
            reason: this.meeting.reason,
            datetime: this.meeting.datetime,
          }),
        }
      ).then(() => {
        this.mrq = false;
      });
    },
    openForEdit(m) {
      this.currentMeeting = m;
      this.edititem = true;
    },
    updateMeeting() {
      this.loading = true;

      fetch(
        "https://script.google.com/macros/s/AKfycbxCVghzqN-sCd9bsd9i8zlyrgmyMdDaNtqcjwygaZnsM-0FQCXtkRdohybS8uThVA_Q/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            id: this.currentMeeting.meeting_id,
            ...this.currentMeeting,
            type: "edit",
          }),
        }
      ).then(() => {
        this.loading = false;
        this.edititem = false;
        this.getMeetings();
      });
    },

    getMeetings() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxCVghzqN-sCd9bsd9i8zlyrgmyMdDaNtqcjwygaZnsM-0FQCXtkRdohybS8uThVA_Q/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines
            .map((r) => ({
              ...r,
              name: this.students.find((s) => s.sheet.substr(39, 44) == r.roll)
                ?.name,
            }))
            .sort((b, a) => Number(a.meeting_id) - Number(b.meeting_id));
        });
    },
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbzyGzm9L0aVS-6OHqPOjWwdZ91o8gRT8HLuBIOjzHgKVtKepimf9zw6HWLLRuLzx9nxXw/exec?type=students"
      )
        .then((res) => res.json())
        .then((data) => {
          this.students = data.routines.map((k) => ({
            ...k,
            roll: k.sheet.substr(39, 44),
          }));
          this.getMeetings();
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
</style>